import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from "axios";
import { BASE_API_URL } from '@/constants';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductData: [],
    cartLoaded: false,
    orderInfo: {},
  },
  mutations: {
    updateProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((prod) => prod.productId === productId);

      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((prod) => prod.productId !== productId);
    },
    updateUserAccessKey(state, key) {
      state.userAccessKey = key;
    },
    updateCartProductData(state, data) {
      state.cartProductData = data;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
        product: {
          ...item.product,
          image: item.product.image.file.url,
        },
      }));
    },
    clearCart(state) {
      state.cartProducts = [];
      state.cartProductData = [];
    },
    setCartLoaded(state, value) {
      state.cartLoaded = value;
    },
    setOrderInfo(state, value) {
      state.orderInfo = value;
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts;
    },
    cartTotalPrice(state) {
      return state.cartProducts.reduce(
        (acc, item) => (item.product.price * item.amount) + acc,
        0,
      );
    },
    cartTotalAmount(state) {
      return state.cartProducts.reduce(
        (acc, item) => item.amount + acc,
        0,
      );
    },

  },
  actions: {
    loadCart(context) {
      context.commit('setCartLoaded', false);
      return axios
        .get(
          `${BASE_API_URL}/baskets`, {
            params: {
              userAccessKey: context.state.userAccessKey ? context.state.userAccessKey : '',
            },
          },
        )
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductData', response.data.items);
          context.commit('syncCartProducts');
          context.commit('setCartLoaded', true);
        });
    },
    addProductToCart(context, { productId, amount }) {
      return axios
        .post(
          `${BASE_API_URL}/baskets/products`, {
            productId,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey ? context.state.userAccessKey : '',
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateProductAmount(context, { productId, amount }) {
      context.commit('updateProductAmount', { productId, amount });
      if (amount < 1) {
        return Promise.resolve();
      }
      return axios
        .put(
          `${BASE_API_URL}/baskets/products`, {
            productId,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey ? context.state.userAccessKey : '',
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductData', response.data.items);
        }).catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteProduct(context, productId) {
      return axios
        .delete(
          `${BASE_API_URL}/baskets/products`,
          {
            params: {
              userAccessKey: context.state.userAccessKey ? context.state.userAccessKey : '',
            },
            data: {
              productId,
            },
          },
        )
        .then((response) => {
          context.commit('updateCartProductData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    sendOrder(context, formData) {
      return axios
        .post(
          `${BASE_API_URL}/orders`, {
            ...formData,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('clearCart');
          context.commit('setOrderInfo', response.data);
          return response;
        });
    },
    loadOrder(context, orderId) {
      return axios
        .get(
          `${BASE_API_URL}/orders/${orderId}`,
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          },
        )
        .then((response) => {
          context.commit('setOrderInfo', response.data);
          return response;
        });
    },
  },
});
