import BaseField from "@/components/BaseFormField.vue";

export default {
  props: ['title', 'error', 'placeholder', 'value'],
  components: {
    BaseField,
  },
  computed: {
    fieldData: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
};
