<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click="amountDown">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="currentAmount" name="count" />

    <button type="button" aria-label="Добавить один товар" @click="amountUp">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>

  </div>
</template>

<script>
export default {
  model: {
    prop: 'amount',
    event: 'change',
  },
  props: {
    amount: Number,
  },
  computed: {
    currentAmount: {
      get() {
        return this.amount;
      },
      set(value) {
        this.$emit('change', value);
      },
    },
  },
  methods: {
    amountUp() {
      this.currentAmount += 1;
    },
    amountDown() {
      if (this.currentAmount > 1) {
        this.currentAmount -= 1;
      }
    },
  },
};
</script>

