<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> {{ productsTotal }} товаров </span>
    </div>
    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category.sync="filterCategory"
        :color.sync="filterColor"
      />
      <section class="catalog">
        <div v-if="productLoadError">Ошибка загрузки</div>
        <div v-else-if="!productLoaded">Загрузка товаров...</div>
        <ProductList v-else :products="products" />

        <BasePaginator
          v-model="productPage"
          :total="productsTotal"
          :perPage="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import products from "@/data/products";
import BasePaginator from "@/components/BasePaginator.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import { BASE_API_URL } from '@/constants';
import axios from "axios";

export default {
  data() {
    return {
      productPage: 1,
      productsPerPage: 3,
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategory: 0,
      filterColor: 0,
      productData: null,
      productLoaded: false,
      productLoadError: false,
    };
  },
  components: {
    ProductList,
    BasePaginator,
    ProductFilter,
  },
  computed: {
    productsTotal() {
      return this.productData ? this.productData.pagination.total : 0;
    },
    filteredProducts() {
      let res = products;

      if (this.filterPriceFrom > 0) {
        res = res.filter((prod) => prod.price > this.filterPriceFrom);
      }

      if (this.filterPriceTo > 0) {
        res = res.filter((prod) => prod.price < this.filterPriceTo);
      }

      if (this.filterCategory > 0) {
        res = res.filter((prod) => prod.categoryId === this.filterCategory);
      }

      if (this.filterColor > 0) {
        res = res.filter((prod) => prod.colors.includes(this.filterColor));
      }

      return res;
    },
    products() {
      return this.productData
        ? this.productData.items.map((item) => ({
          ...item,
          image: item.image.file.url,
        }))
        : [];
      // const offset = (this.productPage - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    },
  },
  watch: {
    productPage() {
      this.loadProducts();
    },
    filterCategory() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  methods: {
    loadProducts() {
      clearTimeout(this.loadProductsTimer);
      this.productLoaded = false;
      this.productLoadError = false;
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(
            `${BASE_API_URL}/products`, {
              params: {
                page: this.productPage,
                limit: this.productsPerPage,
                categoryId: this.filterCategory,
                minPrice: this.filterPriceFrom,
                maxPrice: this.filterPriceTo,
                colorId: this.filterColor,
              },
            },
          )
          .then((response) => {
            this.productData = response.data;
          })
          .catch(() => {
            this.productLoadError = true;
          }).then(() => {
            this.productLoaded = true;
          });
      }, 0);
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
